import {Button,Container,Form,Nav,NavDropdown,Navbar} from 'react-bootstrap'
import Bck1 from '../assets/bckTop.jpg'
import ContentHeader from './contentHeader'

 const Nave = ()=>{
    return(
        <>
        <div style={{backgroundImage: `url(${Bck1})`,width:'100%',height:'100vh'}}>

        <Navbar expand="lg" style={{paddingTop:'7vh',paddingLeft:'4vh',paddingRight:'3vh'}}>
      <Container fluid>
        <Navbar.Brand href="#">Navbar scroll</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
           
            <Nav.Link href="#action1" style={{marginLeft:'20vh',fontSize:'4vh',color:'white'}}>Home</Nav.Link>
            <Nav.Link href="#action2" style={{fontSize:'4vh',color:'white'}}>Link</Nav.Link>
            <NavDropdown title="Link" id="navbarScrollingDropdown" style={{fontSize:'4vh',color:'red'}}>
              <NavDropdown.Item href="#action3" style={{fontSize:'4vh'}}>Action</NavDropdown.Item>
              <NavDropdown.Item href="#action4" >
                Another action
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action5"  >
                Something else here
              </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link href="#" disabled style={{fontSize:'4vh',color:'white'}}>
              Link
            </Nav.Link>
          </Nav>

          
          <Form className="d-flex">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-light">Search</Button>
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    {/* contentHEader--------------- */}
    <ContentHeader/>
    </div>
        </>
    )
}

export default Nave