import myImg from '../assets/bottle.png'
import { Image,Container,Col,Row } from 'react-bootstrap'
import "../components.css"
import {useTypewriter,Cursor} from 'react-simple-typewriter'

const ContentHeader =()=>{
    const [text]=useTypewriter({
        words:['scented','exlusive','royal'],
        loop:{},
        typeSpeed:120,
    })
    return(
        <>
        <Container>
            <Row style={{display:'flex',justifyContent:'center',alignItems:'center',paddingTop:'20vh'}}>
            
            <Col xl={6} lg={6} md={7}sm={6} xs={6} style={{display:'inline-block'}}>
                
                    <h1 style={{fontFamily:'Bahnschrift Condensed',color:'white',textAlign:'center'}}>
                    a perfume that &nbsp; 
                    <span style={{fontWeight:'bold',color:'yellow'}}>{text}</span>
                    <span style={{color:'black'}}>
                        <Cursor cursorStyle='|'/>
                    </span>
                    </h1>
                    <h4 style={{fontFamily:'Bahnschrift Light',color:'white',textAlign:'center'}}>makes your girlfriend want to bite your neck</h4>
                
            </Col>
            <Col xl={6} lg={6} md={5} sm={6} xs={12}>
                <Image src={myImg} style={{height:'43vh',opacity:'0.5'}} />
            </Col>
            </Row>
        </Container>    
            
        
        </>
    )
}
export default ContentHeader