// import { Button } from "react-bootstrap"
import Content1 from "./content1"
import Content2 from "./content2"
import Content3 from "./content3"
import Content4 from "./content4"
import Content5 from "./content5"
import Content6 from "./content6"
import Content7 from "./content7"
import Footer from "../footer/information"
import { useState } from "react"
import { Col,Container,Row } from "react-bootstrap"


const Allcontent=()=>{
    const [theme,setTheme]=useState({
        background:'white',
        colorFontSecundary:'#741b47',
        colorFontPrimrary:'white',
        indexS:'1',
        indexP:'-1',
        button:'outline-light',
        shadow:'grey',

        ChangeThemP:()=>{
            setTheme(data=>{
                return{
                    ...data,
                    colorFontPrimrary:'#741b47',
                    colorFontSecundary:'white',
                    background:'#741b47',
                    indexS:'-1',
                    indexP:'1',
                    button:'outline-danger',
                    shadow:'black'
                }
            })
        },
        ChangeThemS:()=>{
            setTheme(data=>{
                return{
                    ...data,
                    colorFontPrimrary:'white',
                    colorFontSecundary:'#741b47',
                    background:'white',
                    indexS:'1',
                    indexP:'-1',
                    button:'outline-light',
                    shadow:'white'

                }
            })
        }
    })

   
        
    return(
        <>
        <div style={{backgroundColor:`${theme.background}`}} >
            <Container fluid>
            <Row style={{paddingTop:'20vh'}}>
                
                <Col style={{display:'flex',justifyContent:'center'}}>
            {/* light */}
            <div style={{position:'absolute',cursor:'pointer',color:`white`,zIndex:`${theme.indexP}`}} onClick={()=>theme.ChangeThemS()}>
            <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-brightness-high" viewBox="0 0 16 16" >
            <path d="M8 11a3 3 0 1 1 0-6 3 3 0 0 1 0 6m0 1a4 4 0 1 0 0-8 4 4 0 0 0 0 8M8 0a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 0m0 13a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 13m8-5a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2a.5.5 0 0 1 .5.5M3 8a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2A.5.5 0 0 1 3 8m10.657-5.657a.5.5 0 0 1 0 .707l-1.414 1.415a.5.5 0 1 1-.707-.708l1.414-1.414a.5.5 0 0 1 .707 0m-9.193 9.193a.5.5 0 0 1 0 .707L3.05 13.657a.5.5 0 0 1-.707-.707l1.414-1.414a.5.5 0 0 1 .707 0zm9.193 2.121a.5.5 0 0 1-.707 0l-1.414-1.414a.5.5 0 0 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .707M4.464 4.465a.5.5 0 0 1-.707 0L2.343 3.05a.5.5 0 1 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .708z"/>
            </svg>
            </div>
            {/* dark */}
            <div style={{position:'absolute',display:'inline-block',color:`${theme.colorFontSecundary}`,cursor:'pointer',zIndex:`${theme.indexS}`}} onClick={()=>theme.ChangeThemP()}>
            <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-moon" viewBox="0 0 16 16" >
            <path d="M6 .278a.768.768 0 0 1 .08.858 7.208 7.208 0 0 0-.878 3.46c0 4.021 3.278 7.277 7.318 7.277.527 0 1.04-.055 1.533-.16a.787.787 0 0 1 .81.316.733.733 0 0 1-.031.893A8.349 8.349 0 0 1 8.344 16C3.734 16 0 12.286 0 7.71 0 4.266 2.114 1.312 5.124.06A.752.752 0 0 1 6 .278M4.858 1.311A7.269 7.269 0 0 0 1.025 7.71c0 4.02 3.279 7.276 7.319 7.276a7.316 7.316 0 0 0 5.205-2.162c-.337.042-.68.063-1.029.063-4.61 0-8.343-3.714-8.343-8.29 0-1.167.242-2.278.681-3.286z"/>
            </svg>
            </div>
            </Col>
            </Row>
            </Container>
                
        <Content1 color={theme.colorFontSecundary}/>
        <Content2 color={theme.colorFontSecundary}/>
        <Content3 color={theme.colorFontSecundary} colorP={theme.colorFontPrimrary} button={theme.button} shadow={theme.shadow}/>
        <Content4 color={theme.colorFontSecundary}/>
        <Content5 colorP={theme.colorFontSecundary}/>
        <Content6 color={theme.colorFontSecundary} colorP={theme.colorFontPrimrary} button={theme.button}/>
        <Content7 color={theme.colorFontSecundary}/>
        <Footer color={theme.colorFontSecundary} colorP={theme.colorFontPrimrary}/>

        {/* div tengah */}
        {/* <div style={{position:'relative',display:'grid',justifyContent:'center'}}>
            <div style={{position:'relative',width:'30vh',height:'30vh',backgroundColor:'yellow'}}>

            </div>
        </div> */}
        </div>
        </>
    )
}
export default Allcontent