import { Col } from 'react-bootstrap'

import '../components.css'
const Content4=(props)=>{
    return(
        <>
        
            <div className='row gx-0' style={{marginTop:"20vh"}}>
                
                <Col >
                <div style={{position:'relative',height:'120vh',backgroundColor: `${props.color}`,cursor:'pointer'}} className='wobble-hor-bottom'></div>
                </Col>
                <Col >
                <div  style={{position:'relative',height:'120vh',backgroundColor:'#8A33E1',cursor:'pointer'}} className='wobble-hor-bottom'></div>
                </Col>
                <Col >
                <div  style={{position:'relative',height:'120vh',backgroundColor:'palevioletred',cursor:'pointer'}} className='wobble-hor-bottom'></div>
                </Col>
                
            </div>
         
        </>
    )
}
export default Content4